.forecast {
    background-image: url(../src/img/bg.jpg);
    background-size: cover;
    background-position: top left;
    padding-bottom: 30px;
    filter:  grayscale(40%);
    .forecast-title {  
        color: rgb(209, 231, 238);
        font-size: 44px;
        font-weight: 600;
        font-family: 'Poppins', sans-serif;
        text-align: center;
        padding: 35px 40px 25px;
        text-transform: uppercase;
    }

    .weather {
        border-radius: 20px;
        margin: 0 auto;
        width: 40%;
        background-color: rgba($color: rgb(250, 242, 242), $alpha: 0.5);
        padding: 30px;
        color: rgb(24, 65,91);
        font-family: 'Poppins', sans-serif;
        margin-bottom: 20px;
    }
    .row {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.selectCity {
    margin: 0 auto 15px;
    width: 40%; 
    .select-title {
        color: rgb(209, 231, 238);
        font-family: 'Poppins', sans-serif;
        font-size: 26px;padding-bottom: 5px;
    }
    .input{
        height: 30px;
        width: 40%;
        box-sizing: border-box;
        font-size: 18px;
        border: 1px solid rgb(209, 231, 238);
        padding-left: 5px;
        color: rgb(24, 65,91);
        background-color: rgba(209, 231, 238, 0.5);
    
    }
    .button {
        height: 30px;
        background-color: rgb(24, 65,91);
        border: 1px solid rgb(209, 231, 238);
        color: rgb(209, 231, 238);
        width: 20%;
        // box-sizing: border-box;
        font-family: 'Poppins', sans-serif;
        font-size: 18px;
        padding: 0;
        margin-left: 10px;

    }
}
